import { Component, OnInit, ElementRef, Inject, EventEmitter, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';
import { IssueApproveService,CurrentUserService, DateConverterService, MaterialIssueService } from '../../../shared';
//BranchMasterService, DateFormatService
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';

import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-approve-issue',
  templateUrl: './approve-issue.component.html',
  styleUrls: ['./approve-issue.component.scss']
})
export class ApproveIssueComponent implements OnInit {
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('errNotification') errNotification: jqxNotificationComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('input1') inputEl: ElementRef;
  @ViewChild('myWindow') myWindow: jqxWindowComponent;
  @ViewChild('groupGrid') groupGrid: jqxGridComponent;
  @ViewChild('orderGrid') orderGrid: jqxGridComponent;


  approveReqForm: FormGroup;
  gridSource: any;
  gridDataAdapter: any;
  gridColumns: any = [];
  orderSource: any;
  orderDataAdapter: any;
  orderColumns: any = [];
  branchAdapter: any = [];
  editrow: number = -1;
  showDetailsGrid: boolean = false;
  selectedOrderList: Array<any> = [];
  userData: any = {};

  constructor(
    private fb: FormBuilder,
    // private bms: BranchMasterService,
    private ips: IssueApproveService,
    private cus: CurrentUserService,
    // private dfs: DateFormatService,
    private translate: TranslateService,
    private dateService: DateConverterService,
    private cdr: ChangeDetectorRef,
    private mis: MaterialIssueService
  ) {
    this.createForm();
    // this.getTranslation();
    this.userData = this.cus.getTokenData();
  }

  createForm() {
    this.approveReqForm = this.fb.group({
      'approve_date': [null, Validators.required],
      'approve_by': [null, Validators.required]
    });
  }

  // transData:any;
  // getTranslation(){
  //   this.translate.get(['SN','ISSUELIST','ITEM','NUMBER', 'ISSUEDATE','ITEMNAME','ASSETCODE','ISSUEBY','RECEIVEBRANCH','RECEIVEPERSON','RECEIVESITE','REMARKS']).subscribe((translation: [string]) => {
  //     this.transData = translation;
  //   });
  // }

  ngOnInit() {

    this.gridSource =
      {
        datatype: 'json',
        datafields: [
          { name: 'id', type: 'number' },
          { name: 'issue_no', type: 'string' },
          { name: 'issue_date', type: 'string' },
          { name: 'material_issue_details' },
          { name: 'store_name', type: 'string', map:'store>name' },
          { name: 'store_id', type: 'string' },
          { name: 'to_store_name', type: 'string', map:'to_store>name' },
          { name: 'remarks', type: 'string' },
        ],
        id: 'id',
        localdata: [],
      };

    this.gridDataAdapter = new jqx.dataAdapter(this.gridSource);

    this.gridColumns =
      [
        {
          text: 'S.N.', sortable: false, filterable: false, editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: '', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        { text: 'Issue No', datafield: 'issue_no', },
        { text: 'Issue Date', datafield: 'issue_date' },
        { text: 'Store', datafield: 'store_name' },
        { text: 'To Store', datafield: 'to_store_name' },
        { text: 'Item Name', datafield: 'itemName' },
        { text: 'Issue By', datafield: 'issueBy', width: 250 },
        { text: 'Remarks', datafield: 'remarks' },
        {
          text: 'Action', datafield: 'view', sortable: false, filterable: false, width: 100, columntype: 'button',
          cellsrenderer: (): string => {
            return 'View';
          },
          buttonclick: (row: number): void => {
            this.editrow = row;
            let dataRecord = this.groupGrid.getrowdata(this.editrow);
            // console.log(dataRecord);
            if (dataRecord['issue_no']) {
              this.jqxLoader.open();
              this.orderSource.localdata = dataRecord['material_issue_details'];
              this.orderGrid.updatebounddata();
              this.jqxLoader.close();
            }
          }
        }
      ];

    this.orderSource =
      {
        datatype: 'json',
        datafields: [
          { name: 'item_name', type: 'string', map:'item>name' },
          { name: 'quantity', type: 'string' },
          { name: 'rate', type: 'string' },
          { name: 'remarks', type: 'string' },
        ],
        localdata: [],
      };

    this.orderDataAdapter = new jqx.dataAdapter(this.orderSource);

    this.orderColumns =
      [
        {
          text: 'SN', sortable: false, filterable: false, editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: '', columntype: 'number', width: 80,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        { text: 'Item Name', datafield: 'item_name', },
        { text: 'Quantity', datafield: 'quantity' },
        { text: 'Rate', datafield: 'rate' },
        { text: 'Remarks', datafield: 'remaks' },
      ];
  }

  gridRenderToolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';
    let buttonContainer3 = document.createElement('div');
    buttonContainer3.id = 'buttonContainer3';
    buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer3.innerHTML = "<b>Issue List:</b>";
    container.appendChild(buttonContainer3);
    toolbar[0].appendChild(container);
  };

  orderRenderToolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';
    let buttonContainer3 = document.createElement('div');
    buttonContainer3.id = 'buttonContainer3';
    buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer3.innerHTML = "<b>Issue Pending List Detail:</b>";
    container.appendChild(buttonContainer3);
    toolbar[0].appendChild(container);
  };

  // rowChange(event: any) {
  //   let dataRecord = this.groupGrid.getrowdata(event.args.rowindex);
  //   let dt = {};
  //   dt['itemNo'] = dataRecord['itemNo'];
  //   dt['sn'] = dataRecord['sn'];
  //   this.selectedOrderList.push(dt);
  // }

  // rowUnChange(event: any) {
  //   let dataRecord = this.groupGrid.getrowdata(event.args.rowindex);
  //   let index = this.selectedOrderList.findIndex(x => x.sn == dataRecord['sn']);
  //   if (index > -1) {
  //     this.selectedOrderList.splice(index, 1);
  //   }
  // }


  ngAfterViewInit() {
    this.getIssues();
    let dateD = this.dateService.getToday();
    console.info(dateD);
    setTimeout(() => {
      this.approveReqForm.controls['approve_date'].setValue(dateD['fulldate']);
      this.approveReqForm.get('approve_date').markAsTouched();
      this.approveReqForm.controls['approve_by'].setValue(this.userData['user'].username);
      this.approveReqForm.get('approve_by').markAsTouched();
    }, 100);
    this.cdr.detectChanges();
  }

  getIssues(){
    let getData = { pending : true, limit: 'all' };
    this.mis.index(getData).subscribe(
    response => {
        this.branchAdapter = response;
        this.gridSource.localdata = response;
        this.groupGrid.updatebounddata();  
    },
    error => {
      console.log(error)
    });
  }

  approve(formData) {
    this.jqxLoader.open();
    let id = this.groupGrid.getselectedrowindexes();
    let ids = [];
    for (let i = 0; i < id.length; i++) {
      let dataRecord = this.groupGrid.getrowdata(Number(id[i]));
      ids.push(dataRecord['id']);
    }
    formData['approve_by'] = this.userData['user'] && this.userData['user'].username || null ;

    console.log(ids);
    console.log(formData);
    if (ids.length > 0 && formData['approve_by'] && formData['approve_date']) {
      formData['approve_requisition'] = true;
      this.mis.update('['+ids+']',formData).subscribe(
        result => {
          if (result['message']) {
            this.groupGrid.clearselection();
            this.getIssues();
            let messageDiv: any = document.getElementById('message');
            messageDiv.innerText = result['message'];
            this.msgNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          this.jqxLoader.close();
          console.info(error);
        }
      );
    } else {
      this.jqxLoader.close();
      let messageDiv: any = document.getElementById('error');
      messageDiv.innerText = "Please Select at least one item";
      this.errNotification.open();
    }
  }

  reject(formData) {
    this.jqxLoader.open();
    formData['array'] = this.selectedOrderList;
    formData['status'] = 'Reject';
    if (formData['array'].length > 0) {
      this.ips.storeAprove(formData).subscribe(
        result => {
          if (result['message']) {
            this.groupGrid.clearselection();
            this.groupGrid.updatebounddata();
            let messageDiv: any = document.getElementById('message');
            messageDiv.innerText = result['message'];
            this.msgNotification.open();
          }
          this.jqxLoader.close();
          if (result['error']) {
            this.groupGrid.clearselection();
            this.groupGrid.updatebounddata();
            let messageDiv: any = document.getElementById('error');
            messageDiv.innerText = result['error']['message'];
            this.errNotification.open();
          }
        },
        error => {
          this.jqxLoader.close();
          console.info(error);
        }
      );
    } else {
      this.jqxLoader.close();
      let messageDiv: any = document.getElementById('error');
      messageDiv.innerText = "Please Select at least one item";
      this.errNotification.open();
    }
  }

}



